import React, { useState, useEffect } from 'react';

// reactstrap components
import { Card, CardHeader, CardBody, Row, Col } from 'reactstrap';

// core components
import PanelHeader from '../components/PanelHeader/PanelHeader.jsx';
import NMRDisplayer from '../../component/NMRDisplayer.jsx';
import { Analysis } from '../../data/Analysis';

function loadData() {
  return new Promise((resolve, reject) => {
    fetch('./json-files/1HSpectrum.json')
      .then((response) => checkStatus(response) && response.json())
      .then((data) => {
        Analysis.build(data).then((obj) => {
          resolve(obj);
        });
      })
      .catch((err) => {
        reject(err);
      }); // Never forget the final catch!
  });
}

function checkStatus(response) {
  if (!response.ok) {
    throw new Error(`HTTP ${response.status} - ${response.statusText}`);
  }
  return response;
}

const Spectrum1H = () => {
  const [data, setData] = useState();

  useEffect(() => {
    loadData().then((d) => {
      setData(d);
    });
  }, []);

  return (
    <>
      <PanelHeader size="sm" />
      <div className="content">
        <Row>
          <Col md={12}>
            <Card>
              <CardHeader>
                <h5 className="title">NMR Displayer</h5>
                <p className="category">1H spectrum test</p>
              </CardHeader>
              <CardBody>
                <NMRDisplayer data={data} />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default Spectrum1H;
